@import url('https://fonts.googleapis.com/css2?family=Marianne&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Marianne', sans-serif;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

ul {
  display: flex;
  gap: 20px;
}

h1 {
  font-size: 3rem;
  color: white;
  text-align: center;
  padding: 100px 0;
}

.button-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-items: center;
  align-items: center;
  padding: 50px 0;
}

.button-row {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  font-family: 'Marianne', sans-serif;
  padding: 10px 20px;
  margin-bottom: 10px;
  cursor: pointer;
}

p {
  font-size: 1.2rem;
}

.section {
  text-align: center;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
