.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}

.navbar .logo1,
.navbar .logo2 {
    max-width: 100px;
    height: auto;
}

.nav-items {
    display: flex;
    align-items: center;
    list-style: none;
}

.nav-items li {
    margin-right: 20px; /* Ajoutez cette ligne pour ajouter une marge à droite pour chaque élément de la liste */
}

.nav-items li:last-child {
    margin-right: 0; /* Ajoutez cette ligne pour supprimer la marge à droite du dernier élément de la liste */
}



.navbar .logo1,
.navbar .logo2 {
    display: none;
    max-width: 100px; /* Modifier la taille souhaitée ici */
    height: auto;
}

