.buttons-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
    justify-items: center;
    align-items: center;
    width: 50%;
    margin: 0 auto;
}

.button-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 1rem;
}

.button-image {
    width: 200px;
    height: auto;
    margin-bottom: 0.5rem;
}
